html {
  margin: 0;
  padding: 0;
}

body {
  padding: 0;
  margin: 0;
  /*background-color: #f9c416;*/
  border: 1px solid #272727;
  border-bottom: 24px solid #272727;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: initial;
  overflow: hidden;
  /*color: #272727;*/
}

* {
  outline: none;
}

#status-bar {
  height: 24px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.22), 0 1px 2px rgba(0, 0, 0, 0.3);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
}

#title-bar {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 1px 1px rgba(0, 0, 0, 0.1);
  -webkit-app-region: drag;
  height: 32px;
  padding: 0;
  margin: 0;
  color: #272727;
  font-family: sans-serif;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

#title {
  position: fixed;
  top: 0;
  left: 12px;
  line-height: 32px;
  font-size: 14px;
}

#title img {
  padding: 5px;
}

#title-bar-btns {
  -webkit-app-region: no-drag;
  position: fixed;
  top: 0;
  right: 0;
}

#title-bar-btns button {
  height: 32px;
  width: 32px;
  background-color: transparent;
  border: none;
  color: #272727;
  font-size: 16px;
}

#title-bar-btns button:hover {
  background-color: #272727;
  color: #fff;
}

#page {
  position: fixed;
  left: 0;
  right: 0;
  top: 32px;
  bottom: 24px;
  /*color: #272727;*/
}

a {
  color: #272727;
}

#status {
  color: #fff0ff;
  position: fixed;
  left: 5px;
  bottom: 0;
}

* {
  -webkit-user-select: none;
  user-select: none;
}
